<template lang="pug">
section.page-admin-back
  div(grid)
    div(block v-if="$route.query.action === 'transfer'")
      .actions(row)
        button.action(@click="update_query({action: null, filters: null})") {{ t.back }}
        .action-title {{ t.ph_transfer }}
      div(row)
        fieldset(style="margin-right: auto; padding-left: 10px;")
          label(for="filter_string") {{ t.filter_string }}
          input(id="filter_string" type="text" placeholder="Nom client, code ph, ref ..." @input="update_query({filters: $event.target.value})" :value="$route.query.filters")
        div(column)
          .checkbox(row @click="update_query({include_no_dispo: ($route.query.include_no_dispo) ? null : 'on'})")
            .box(:class="{active: $route.query.include_no_dispo}")
            .label Inclure les ph qui n'ont pas donn leur dispos
          .checkbox(row @click="update_query({include_full_quota: ($route.query.include_full_quota) ? null : 'on'})")
            .box(:class="{active: $route.query.include_full_quota}")
            .label Inclure les ph qui ont rempli leur quota
        .legend-container
          .legend(row)
            .color.close
            .label Ph ayant une sance adjacente
          .legend(row)
            .color.day
            .label ph ayant une sance dans la journe
          .legend(row)
            .color.filled
            .label ph dispo
      .frame
        div(column)
        .header(row)
          .cell(row center :class="[col.size && col.size]" v-for="col in transfer_cols") {{ t[col.name] || col.name }}
          .cell.xl(row center) Actions
        .lines(v-if="transfer_phs")
          vue-collapse(v-for="ph in filtered_transfer_phs" :opened="$route.query.photographer === ph.photographer_id")
            template(slot="header")
              .line(row :class="[ph.category]")
                template(v-for="col in transfer_cols")
                  .cell(row center :class="[col.size && col.size, (ph.available_invitations === 0) && 'disabled']" v-if="col.name === 'available_invitations'") {{ ph[col.name] }}
                  .cell(row center :class="[col.size && col.size]" v-else) {{ ph[col.name] }}
                .cell.xl(row center)
                  button.action.primary(@click="$root.warn('bientt!')") {{ t.transfer }}
                  button.action(row center @click="update_query({photographer: ($route.query.photographer === ph.photographer_id) ? null : ph.photographer_id})") Planning
            template(slot="content")
              .more
                .planning(row center)
                  .slot(row :class="{active: hour==14}" v-for="hour in [9,10,11,12,13,14,15,16,17]")
                    .time {{ hour }}h
                    .time.minutes 30
                    .box(:class="{booked: [9,10,12,15].includes(hour)}")
                    .box(:class="{booked: [9,10,12,13].includes(hour)}")
    div(block v-else)
      div(row style="padding: 10px 0;")
        fieldset
          label(for="filter_string") {{ t.filter_string }}
          input(id="filter_string" type="text" placeholder="Nom client, code ph, ref ..." @input="update_query({filters: $event.target.value})" :value="$route.query.filters")
        .actions(row)
          button.action.primary(@click="" :class="{inactive: true}") {{ t.save_changes }}
          button.action.primary(@click="filtered_sessions.map(d => sessions[d.session_id]).dlCSV(',', 'sessions.csv')") {{ t.export_csv }}
      .frame
        div(column)
        .header(row)
          .cell(row center :class="[col.size && col.size, col.category && col.category]" v-for="col in main_cols") {{ t[col.name] || col.name }}
          .cell(row center) {{ t.more }}
        .lines
          vue-collapse(v-for="session in filtered_sessions" :opened="$route.query.session === session.session_id")
            template(slot="header")
              .line(row)
                template(v-for="col in main_cols")
                  .cell(row center :class="[col.size && col.size]") {{ session[col.name] }}
                .cell(row center)
                  button.action(row center :class="{active: $route.query.session === session.session_id}" @click="update_query({session: ($route.query.session === session.session_id) ? null : session.session_id})") Actions
            template(slot="content")
              .more
                div(row right)
                  button.flat(@click="update_query({session: session.session_id, action: 'transfer', filters: null, include_full_quota: 'on'})") [DEMO] {{ t.transfer }}
                  button.action.primary
                    a(:href="typeform_ph(session)" target="_blank") Typeform PH
                //- button.action b

</template>
<script>
window.debounce = Vue.prototype.debounce = (fn, delay) => {
  var timeoutID = null
  return function () {
    clearTimeout(timeoutID)
    var args = arguments
    var that = this
    timeoutID = setTimeout(function () {
      fn.apply(that, args)
    }, delay)
  }
}
export default {
  data() {
    const main_cols = [
              {name:'session_id',type:'string'},
              {name:'date',type:'string'},
              {name:'hour',type:'string', size: 'small'},
              {name:'client_firstname',type:'string', category: 'client', size: 'large'},
              {name:'client_email',type:'string', category: 'client'},
              {name:'client_phone',type:'string', category: 'client'},
              {name:'photographer_id',type:'string', category: 'photographer', size: 'small'},
              {name:'city',type:'string'},
            ]
    const transfer_cols = [
            {name:'photographer_id',type: 'string'},
            {name:'ph_firstname',type: 'string', size:'small',edit: true},
            {name:'ph_phone',type: 'string', edit: true},
            {name:'ph_email',type: 'string', size:'large', edit: true},
            {name:'ph_ranking',type: 'string', size:'small', edit: true},
            {name:'available_invitations',type:'string'},
    ]
    var typeform = 'https://everphotoshoot1.typeform.com/to/gvbcWe?refycbm=BCEZ-ZMHR-UCYL&emailclient=test_infra+estelle.moradei27@everphotoshoot.com&name=Estelle&city=Marseille&starttime=2018-05-26T14:00:00+02:00'
    return {
      sessions: null,
      typeform,
      main_cols,
      filter_string:null,
      search: null,
      transfer_phs: null,
      transfer_cols,
      demo_close: [['VILZ',2], ['CHRO',1], ['JEBA',0]],
      demo_day: [['JUMY',5], ['FLHE',3], ['BENZ',2], ['LAFE', 0], ['LOBI',0]],
      demo_filled : [['LUHA',5], ['MLRO',3], ['MIKA',2], ['YATA', 0], ['THOR',0]]
    }
  },
  computed: {
    filtered_transfer_phs() {
      if (!this.transfer_phs) return
      const filtered = this.transfer_phs.filter(d => (this.$route.query.include_full_quota || d.available_invitations !== 0) && (this.$route.query.include_no_dispo || d.category))//.v().map(d => d.filter((v,k) => this.main_cols.map(d => d.name).includes(k)))
      if (!this.search) return filtered
      return filtered.filter(d => this.search.every(s => d.some(v => new RegExp(s, 'i').test(v))))
    },
    filtered_sessions() {
      if (!this.sessions) return
      const filtered = this.sessions.v()//.v().map(d => d.filter((v,k) => this.main_cols.map(d => d.name).includes(k)))
      if (!this.search) return filtered
      return filtered.filter(d => this.search.every(s => d.some(v => new RegExp(s, 'i').test(v))))
    },
    active_session() {
      if (!this.$route.query.session || !this.sessions) return
      return this.sessions[this.$route.query.session]
    }
  },
  watch: {
    '$route.query.filters': debounce(function (search) {
      if (!search) return this.search = null
      return this.search = search.split(' ')
    }, 300),
    '$route.query.action': {
      handler() {
      if (this.$route.query.action === 'transfer' && this.active_session) this.transfer_reload()
      }, immediate: true
    },
    active_session() {
      if (this.$route.query.action === 'transfer') this.transfer_reload()
    }
  },
  methods: {
    reload() {
      return axios.get('https://dev.everphotoshoot.com/adminapi/sql_read', {params: {q: "SELECT session_id, date_start, client_firstname, client_email, client_phone, photographer_id, city FROM full_sessions"}})
            .then(res => this.sessions = res.data.reduce((acc,v) => {
              v.date = Date.create(v.date_start, { fromUTC: true }).format('%d %b %Y',this.$root.lang)
              v.hour = Date.create(v.date_start, { fromUTC: true }).format('%H:%M',this.$root.lang)
              acc[v.session_id] = v
              return acc
            },{}))
    },
    transfer_reload() {
      return axios.get('https://dev.everphotoshoot.com/adminapi/sql_read', {params: {q: `SELECT * FROM photographers WHERE ph_city = '${this.active_session.city}'`}})
                  .then(res => {
                    const transfer_phs = res.data.filter(d => d.ph_ranking).reduce((acc,v) => { acc[v.photographer_id] = v; return acc }, {})
                    this.demo_close.map(d => {transfer_phs[d[0]].available_invitations = d[1]; transfer_phs[d[0]].category = 'close'})
                    this.demo_day.map(d => {transfer_phs[d[0]].available_invitations = d[1]; transfer_phs[d[0]].category = 'day'})
                    this.demo_filled.map(d => {transfer_phs[d[0]].available_invitations = d[1]; transfer_phs[d[0]].category = 'filled'})
                    this.transfer_phs = transfer_phs.v().sortBy(d => 10 * ['none','day','filled','close'].indexOf(d.category || 'none') + (d.available_invitations || 0), true)
                  })
    },
    typeform_ph(session) {
      const date_start = Date.create(session.date_start, { fromUTC: true }).format('%Y-%m-{ss}T%H:%M:%S{Z}')//2018-05-26T14:00:00+02:00
      return `https://everphotoshoot1.typeform.com/to/gvbcWe?refycbm=${session.session_id}&emailclient=${session.client_email}&name=${session.client_firstname}&city=${session.city}&starttime=${date_start}`
    }
  },
  mounted() {
    this.reload()
  }
}
</script>
<style scoped>
.planning {  }
.planning .slot { position: relative;margin-top: 20px; }
.planning .slot.active { outline: 4px solid black;}
.planning .box { border: 1px solid rgba(0,0,0,0.2); border-right: none; width:70px; height: 60px; background: var(--primary);}
.planning .slot:last-child .box:last-child { border-right: 1px solid rgba(0,0,0,0.4);}
.planning .time { position: absolute; top: -20px; left:-10px; width: 20px; text-align: center; font-weight: 600;}
.planning .time.minutes {font-weight: 400; left: calc(50% - 10px); font-size: 85%;}
.planning .box.booked { background: rgba(0,0,0,0.5); }

.actions { padding: 20px; }
.header .cell.client { color: rgba(255, 121, 57, 0.7);}
.header .cell.photographer { color: var(--text);}
button.active { box-shadow: none; background: var(--primary);}
.more { margin: 0 20px;  border-bottom: 1px solid rgba(0, 0, 0, .2); padding: 20px 10px; background: rgba(0,0,0,0.025);}
.more button.flat { background: rgba(0,0,0,0.1); }
.more button.flat:hover { background: rgba(0,0,0,0.15); }
.line.day { background: rgba(250, 212, 25, 0.5);}
.line.filled { background: rgba(255, 121, 57, 0.5);}
.line.close { background:  rgba(46, 200, 193, .5) }
.line.day:hover, .color.day { background: rgba(250, 212, 25, 0.7) }
.line.filled:hover, .color.filled { background: rgba(255, 121, 57, 0.7);}
.line.close:hover, .color.close { background:  rgba(46, 200, 193, .7) }
.color { width: 20px; height: 20px;}
.action-title { margin-right: auto; font-size: 160%; padding-left: 30px;}

</style>



