<template lang="pug">
.modal-warn(column)
  .title Message
  .message {{ data }}
  .buttons(row right)
    button.action.primary(@click="$root.modal.name = null;$root.modal.data = null;") OK
</template>
<script>
export default {
  props: ['data'],
}
</script>
<style scoped>
.modal-warn {
  min-width: 300px;
  min-height: 200px;
}
.message {
  display: flex;
  flex: 1 0;
}
</style>



