<template lang="pug">
section.page-admin-ph(grid-box)
  div(grid)
    div(block v-if="!$route.query.add_ph")
      div(row style="padding: 10px 0;")
        fieldset
          label(for="filter_string") {{ t.filter_string }}
          input(id="filter_string" type="text" placeholder="CODE, email@gmail.com, firstname ..." v-model="filter_string")
        .actions(row)
          button.action.primary(@click="post_overrides()" :class="{inactive: !overrides.filter((v,k) => v.keys().length).keys().length}") {{ t.save_changes }}
          button.action.primary(@click="filtered_photographers.map(d => photographers[d.photographer_id]).dlCSV(',', 'photographers.csv', ['photographer_id','ph_email','ph_phone'])") {{ t.export_csv }}
          button.action.primary(@click="update_query({add_ph: 'on'})") {{ t.new_ph }}
      .frame
        div(column)
        .header(row)
          .cell(row center :class="[col.size && col.size]" v-for="col in main_cols") {{ t[col.name] || col.name }}
          .cell(row center) {{ t.more }}
        .lines(v-if="filtered_photographers")
          vue-collapse(v-for="ph in filtered_photographers.first(50)" :opened="$route.query.photographer === ph.photographer_id")
            template(slot="header")
              .line(row)
                template(v-for="col in main_cols")
                  .cell(row center :class="[col.size && col.size]" v-if="col.name === 'photographer_id'")
                    div(style="text-align: center;") {{ ph.photographer_id }}
                    button.reset.secondary(v-if="overrides[ph.photographer_id] && overrides[ph.photographer_id].keys().length" @click="reset_override(ph.photographer_id)") reset
                  .cell(row center :class="[col.size && col.size, overrides[ph.photographer_id] && overrides[ph.photographer_id][col.name] && 'override']" v-else-if="col.values")
                    select(@change="edit(ph.photographer_id,col.name,$event.target[$event.target.selectedIndex].value)")
                      option(v-for="value in col.values" :value="value" :selected="overrides[ph.photographer_id] && overrides[ph.photographer_id][col.name] ? (value === overrides[ph.photographer_id][col.name]) : (value === ph[col.name])") {{ value }}
                  .cell(row center :class="[col.size && col.size, overrides[ph.photographer_id] && overrides[ph.photographer_id][col.name] && 'override']" v-text="(overrides[ph.photographer_id] && overrides[ph.photographer_id][col.name]) || ph[col.name]" :contenteditable="col.edit" @blur="edit(ph.photographer_id,col.name,$event.target.innerText)" v-else)
                .cell(row center style="padding: 0;")
                  //- .plus-button(row center @click="$root.modal.name = 'editph'; $root.modal.data = other_cols.clone().map(d => {d.original_value = photographers[ph.photographer_id][d.name]; return d})")
                    svg-icon(name="pt-icon-more")
                  .plus-button(row center :class="{active: $route.query.photographer === ph.photographer_id}" @click="update_query({photographer: ($route.query.photographer === ph.photographer_id) ? null : ph.photographer_id})")
                    svg-icon(name="pt-icon-more")
            template(slot="content")
              .more(grid)
                fieldset(v-for="col in other_cols")
                  label {{ col.name }}
                  textarea(:value="(overrides[ph.photographer_id] && overrides[ph.photographer_id][col.name]) || ph[col.name]" @change="edit(ph.photographer_id,col.name,$event.target.value)" v-if="col.type === 'text'")
                  input(:value="(overrides[ph.photographer_id] && overrides[ph.photographer_id][col.name]) || ph[col.name]" @change="edit(ph.photographer_id,col.name,$event.target.value)" v-else)
    div(block v-else)
      .actions(row)
        button.action(@click="update_query({add_ph: null})") {{ t.back }}
        button.action.secondary(@click="new_ph = {}") Clear
        button.action.primary(@click="add_ph") {{ t.add_ph }}
      div(grid)
        fieldset(v-for="col in main_cols")
          label.bold {{ t[col.name] || col.name }}*
          select(v-model="new_ph[col.name]" v-if="col.values")
            option(v-for="value in col.values" :value="value") {{ value }}
          textarea(v-model="new_ph[col.name]" v-else-if="col.type === 'text'")
          input(v-model="new_ph[col.name]" v-else)
        fieldset(v-for="col in other_cols")
          label {{ t[col.name] || col.name }}
          textarea(v-model="new_ph[col.name]" v-if="col.type === 'text'")
          input(v-model="new_ph[col.name]" v-else)
</template>
<script>
window.debounce = Vue.prototype.debounce = (fn, delay) => {
  var timeoutID = null
  return function () {
    clearTimeout(timeoutID)
    var args = arguments
    var that = this
    timeoutID = setTimeout(function () {
      fn.apply(that, args)
    }, delay)
  }
}
export default {
  data() {
    const main_cols = [{name:'photographer_id',type: 'string'},
          {name:'ph_firstname',type: 'string', size:'small',edit: true},
          {name:'ph_phone',type: 'string', edit: true},
          {name:'ph_email',type: 'string', size:'large', edit: true},
          {name:'ph_ranking',type: 'string', size:'small', edit: true, values:['','New','Bronze','Iron','Silver','Gold']},
          {name:'ph_status',type: 'string', size:'large', edit: true},
          {name:'lang',type: 'string', size:'small', edit: true, values: ['fr','en']},
          {name:'ph_city',type: 'string', size:'small', edit: true},
      ]
    const other_cols = [
      {name: 'intro', type: 'text'},
      {name: 'ph_photo_url', type: 'text'},
      {name: 'ph_comment', type: 'text'},
      {name: 'ph_referral', type: 'string'},
      {name: 'ph_timezone', type: 'string'},
      {name: 'passion', type: 'string'},
      {name: 'famous_ph', type: 'string'},
      {name: 'famous_ph_2', type: 'string'},
      {name: 'year_exp', type: 'string'},
      {name: 'client_rate', type: 'string'},
      {name: 'review_1', type: 'text'},
      {name: 'client_1', type: 'string'},
      {name: 'review_2', type: 'text'},
      {name: 'client_2', type: 'string'},
      {name: 'review_3', type: 'text'},
      {name: 'client_3', type: 'string'},
      {name: 'secret_weapon', type: 'text'},
      {name: 'anecdote', type: 'text'},
      {name: 'billing_city', type: 'string'},
      {name: 'billing_address', type: 'text'},
      {name: 'billing_zipcode', type: 'string'},
      {name: 'billing_iban', type: 'string'},
      {name: 'billing_siret', type: 'string'},
      {name: 'billing_type', type: 'string'}
      ]
  // ['date_created', 'date'],
  // ['ph_gender', 'string'],
  // ['ph_age', 'int'],
//['ph_email_slack', 'string'],
      // ['ph_lastname', 'string'],
// ['available_days', 'string'],
// ['daily_charge','int'],
// ['favorite_types', 'string'],
// ['ph_timezone', 'string'],
// ['ph_ranking', 'string'],
// ['rating','int'],
// ['intro','text'],
// ['ph_photo_url','text'],
// ['ph_city', 'string'],
// ['ph_referral', 'string'],
// ['ph_comment','text'],
// ['billing_city', 'string'],
// ['billing_address','text'],
// ['billing_zipcode', 'string'],
// ['billing_iban', 'string'],
// ['billing_siret', 'string'],
// ['billing_type', 'string']]

    return {
      main_cols,
      other_cols,
      new_ph: {},
      filter_string: null,
      photographers: null,
      search: null,
      testtest: null,
      overrides: {},
    }
  },
  computed: {
    filtered_photographers() {
      if (!this.photographers) return
      const filtered = this.photographers.v()//.map(d => d.filter((v,k) => this.main_cols.map(d => d.name).includes(k)))
      if (!this.search) return filtered
      return filtered.filter(d => this.search.every(s => d.some(v => new RegExp(s, 'i').test(v))))
    }
  },
  watch: {
    filter_string: debounce(function (search) {
      if (!search) return this.search = null
      return this.search = search.split(' ')
    }, 300)
  },
  methods: {
    reload() {
      return axios.get('https://dev.everphotoshoot.com/adminapi/sql_read', {params: {q: "SELECT * FROM photographers"}})
            .then(res => this.photographers = res.data.reduce((acc,v) => {acc[v.photographer_id] = v; return acc},{}))
    },
    edit(ph_id, col_name, value) {
      if (!this.overrides[ph_id]) Vue.set(this.overrides,ph_id,{})
      if (value === this.photographers[ph_id][col_name]) return this.overrides[ph_id] = this.overrides[ph_id].filter((v,k) => k !== col_name)
      Vue.set(this.overrides[ph_id],col_name,value)
    },
    reset_override(ph_id) {
      this.overrides = this.overrides.filter((v,k) => k !== ph_id)
    },
    post_overrides() {
      const payloads = this.overrides.filter((v,k) => v.keys().length).map((v,k) => {v.photographer_id = k; return v}).v()
      payloads.reduce((acc,v) => acc.then(() => axios.post('https://dev.everphotoshoot.com/adminapi/sql_write', {table: 'photographers', data: v})), Promise.resolve())
              .then(() => {
                this.overrides = {}
                this.reload()
              })
    },
    add_ph(el) {
      if (!this.main_cols.map(col => this.new_ph[col.name]).every(d => d)) return this.$root.warn(this.t.fill_mandatory)
      axios.post('https://dev.everphotoshoot.com/adminapi/sql_write', {table: 'photographers', data: this.new_ph})
            .then(() => {
              this.$root.warn('Photographe ' + this.new_ph.photographer_id + ' Ajout!')
              return this.reload()
            }).then(() => {
              this.update_query({add_ph: null, photographer: this.new_ph.photographer_id})
              this.filter_string = this.new_ph.photographer_id
              this.new_ph = {}
            })
            .catch(() => this.$root.warn(this.t.error))

    }
  },
  mounted() {
    this.reload()
  }
}

</script>
<style scoped>
.vue-collapse:nth-of-type(2n) .line { background: rgba(46, 200, 193, .1);}
.vue-collapse:hover .line, .cell.highlight { background: rgba(46, 200, 193, .2); }
.actions { padding: 20px; }
[contenteditable] { cursor: pointer;}
[contenteditable]:focus { background: #fff;  outline: solid 2px var(--primary)!important; box-shadow: 0 1px 14px 0 rgba(94, 119, 255, 0.4); }
.frame select { padding: 5px; opacity: 1;font-weight: inherit; min-width: 80%; border-bottom: none; width: inherit; min-height: none;}
button.reset { padding: 5px; min-width: 50px; font-size: 70%; font-weight: 400; margin: 0;}
.plus-button {
  background: rgba(0, 0, 0, 0.3);
  border-radius: 100%;
  width: 40px;
  height: 40px;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 1px 6px;
  cursor: pointer;
}
.plus-button.active {
  background: var(--primary);
}
.plus-button:active {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 3px 14px;
}
.plus-button svg {
  fill: #fff;
}
.more { margin: 0 20px;  border-bottom: 1px solid rgba(0, 0, 0, .2); padding: 20px 0; background: rgba(0,0,0,0.025);}
.more fieldset { margin: 10px;}
.bold { font-weight: 600;}
</style>



